.contenedor-perfil {
    padding: 30px;
    margin: 10px auto 0px auto;
    width: 75%;
    background-color: #c3bef7;
    border-radius: 20px;
    box-shadow: 10px 10px 10px #a787fb;
    display: flex;
    justify-content: space-between;
}

.imagen-perfil {
    margin: auto;
    width: 180px;
    max-height: 180px;
    border-radius: 30px;
}

.contenedor-texto {
    text-align: center;
}

.texto-perfil {
    margin: 0px;
    padding: 5px;
    font-family: "Times New Roman", Times, serif;
    font-weight: bold;
    line-height: 1rem;
    font-size: 25px;
    color: #effffa;
}

.cargo-perfil {
    margin: 0px;
    font-size: 1rem;
    color: #3900a3;
    padding: 15px;
}
.resena-perfil {
    margin: 0px;
    font-size: 1rem;
    padding: 30px;
    text-align: justify;
    color: #3900a3;
}

.contenedor-perfil:hover {
    transform: scale(1.07);
    transition: transform 0.5s;
}

.link {
    margin: auto;
    text-decoration: none;
}
.imagen-yt {
    margin: auto;
    width: 50px;
    max-height: 180px;
}
